.burger{
	display: inline-block;
	height: 25px;
	width: 25px;
	cursor: pointer;
	position: relative;
	z-index: 10;

	&__line{
		display: block;
		height: 4px;
		width: 100%;
		background-color: black;
		border-radius: 3px;
		margin-bottom: 5px;
		transition: .5s;

		&:last-child{
			margin-bottom:0;
		}
	}
}

.lines{
	.burger{

		&__line{
			background-color: #fff;
			transform-origin: left 50%;


			&:nth-child(1){
				transform: rotate(45deg) ;
			}

			&:nth-child(2){
				visibility: hidden;
				opacity: 0;
			}

			&:nth-child(3){
				transform: rotate(-45deg);
			}
		}
	}
}